@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Francois+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-image: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url(/mia/static/media/bg.3d7e59a8.jpg);
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}


.navContainer{
  width: 100%;
  height: 125px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btns{
  width: 70%;
  height: 60%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.home, .walibtn, .Photos{
  color: white;
  text-decoration: none;
  font-size: 20px;
  font-family: 'Montserrat Alternates', sans-serif;
  transition: all ease-in-out 150ms;
}

.home:hover, .walibtn:hover, .Photos:hover{
  font-size: 22px;
}

.home:active, .walibtn:active, .Photos:active{
  font-size: 20px;
}





.header{
  font-weight: bold;
  font-family: 'Fredoka One', cursive;
  letter-spacing: 7px;
  text-align: center;
  color: rgb(255,255,255,0.7);

  position: absolute;
  top: 80px;
  left: 50%;
  height: 70%;
  transform: translateX(-50%);
  transition: all ease-in-out 250ms;
  cursor: default;
}

.num{
  font-size: 300px;
  color: #4DC247;
  opacity: 0.5;
  position: relative;
  top: 60px;
  transition: all ease-in-out 250ms;
}

.MIA1{
  font-size: 80px;
  transition: all ease-in-out 250ms;
}

.tagline{
  font-size: 45px;
  transition: all ease-in-out 250ms;
}

.num:hover{
  font-size: 302px;
  opacity: 1;
}

.MIA1:hover{
  font-size: 85px;
  color:rgb(255,255,255, 1);
}

.tagline:hover{
  font-size: 47px;
  color:rgb(255,255,255, 1);
}










.Container{
  position: absolute;
  top: 0;
  background: #14171A;
  width: 100%;
  height: 100vh;
  z-index: -1;
}

.wali{
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 50%;
  height: 400px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.picwali{
  margin-bottom: 10px;
  width: 200px;
  border-radius: 50px;
  border: 2px solid darkgrey;
}

.namaWali{
  font-size: 30px;
  font-family: 'Comfortaa', cursive;
  color: white;
}

.descWali{
  font-size: 25px;
  font-family: 'Comfortaa', cursive;
  color: white;
}

.namaWali, .descWali{
  position: relative;
  top: 25px;
}

.gif1{
  position: absolute;
  top: 70%;
  left: -10%;
  width: 150px;
}

.gif2{
  position: absolute;
  top: 30%;
  left: 90%;
  width: 150px;
}

.gif3{
  position: absolute;
  top: -10%;
  left: 10%;
  width: 150px;
}

.gif4{
  position: absolute;
  top: 20%;
  left: -30%;
  width: 100px;
}

.gif5{
  position: absolute;
  top: -10%;
  left: 80%;
  width: 150px;
}

.gif6{
  position: absolute;
  top: 40%;
  left: 5%;
  width: 150px;
}

.gif7{
  position: absolute;
  top: 60%;
  left: 85%;
  width: 150px;
}

@media only screen and (max-width:768px){
  .num{
    font-size: 250px;
    text-align: center;
  }

  .num:hover{
    font-size: 252px;
  }

  .header{
    font-size: 70px;
    top: 67px;
  }

  .MIA1{
    font-size: 70px;
    position: relative;
    left: 10px;
  }

  .MIA1:hover{
    font-size: 72px;
  }

  .tagline{
    font-size: 45px;
    position: relative;
    left: 10px;
  }

  .tagline:hover{
    font-size: 47px;
  }

  .btns{
    width: 90%;
  }

  .gif1,.gif2,.gif3,.gif4, .gif5, .gif6, .gif7{
    display: none;
  }

  .namaWali{
    font-size: 20px;
  }
  
  .namaWali, .descWali{
    width: 300px;
    text-align: center;
  }
}



